export default {
	data () {
		return {
			text: 0,
			text2: '...',
			state: false,
			state2: false
		}
	},
    onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
        console.log(option.id); //打印出上个页面传递的参数。
        console.log(option.name); //打印出上个页面传递的参数。
		uni.$emit('update',{msg:'页面更新'})
    },
	methods: {
		setText () {
			var _this = this
			_this.text = _this.text + 1;
			if (_this.text >= 9) {
				_this.state = true;
			} 
			console.log(_this.state, '_this.state')
			uni.setTabBarBadge({
			  index: 0,
			  text: _this.state ? _this.text2 : JSON.stringify(_this.text)
			})
		},
		getText () {
			var _this = this
			_this.text = JSON.parse(_this.text - 1);
			if (_this.text <= '9') {
				_this.state = false
			} 
			uni.setTabBarBadge({
			  index: 0,
			  text: _this.state ? _this.text2 : JSON.stringify(_this.text),
			  success(res) {
				if (_this.text <= '0') {
					_this.text = 0;
				  uni.removeTabBarBadge({
					index: 0
				  })
				 }
			  	console.log(res, 'res');
			  }
			})
		},
		openMask () {
			this.state2 = true;
		},
		removeText () {
			uni.removeTabBarBadge({
				index: 0
			});
		}
	}
}
